/*
! Blog - Search bar | EJ
*/
/* Styles au hover */
.header_v3_blog-field:hover {
  box-shadow: 0px 0px 0px 1px rgba(204, 94, 86, 0.5); /* Box shadow au hover */
}

.header_v3_blog-field:hover::placeholder {
  color: #9d362f; /* Couleur du placeholder au hover */
}

/* Styles au focus */
.header_v3_blog-field:focus {
  box-shadow: 0px 0px 0px 2px rgba(204, 94, 86, 0.5); /* Box shadow au focus */
}

.header_v3_blog-field:focus::placeholder {
  color: #cc5e56; /* Couleur du placeholder au focus */
}

/* Style pour le placeholder avec un poids de 500 */
.header_v3_blog-field.placeholder-active {
  font-weight: 500; /* Poids de police modifié lorsque l'utilisateur saisit */
}

/* 
! BLOG - MEDIA QUERY | EJ
*/
@media (min-width: 991px) and (max-width: 1280px) {
  .ressources_big-card.is-blog {
    width: 20rem;
    height: 31.62rem;
  }
  .ressources_bc-top-row {
    height: 16.875rem;
    border-radius: 2.5rem 0.5rem !important ;
  }
  .blog_a-la-une_cards-container {
    flex-direction: row;
    justify-content: left;
  }
  .blog_a-la-une_right-col {
    flex-direction: row;
    flex-flow: row;
  }
  .blog_a-la-une_item {
    width: 100%;
  }

  /* BLOG => 2nd part */
  .blog_last-posts_collections-wrapper {
    flex-direction: column;
  }
  .blog_last-posts_right-col {
    width: 100%;
  }
  .blog_last-posts_selection-list {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }
}

/* 
! HUB | EJ
*/

.hp_blog_sc_descriptive-text {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  line-clamp: 2; /* Propriété standard pour anticiper la compatibilité future */
  overflow: hidden;
}

/* 
! HUB - MEDIA QUERY | EJ
*/

@media (max-width: 390px) {
  .hub_galerie_grid {
    grid-template-columns: 1fr; /* Une seule colonne */
    gap: 0 !important;
  }
}

@media (max-width: 1300px) and (min-width: 992px) {
  .hub_testimonial_grid {
    grid-template-columns: repeat(1, 1fr); /* 2 at 1 col */
  }
  .hub_testimonial_hover-card {
    width: 30rem;
  }
  .hub_guide_grid {
    grid-template-columns: repeat(2, 1fr) !important; /* 3 at 2 col */
  }
}

@media (max-width: 1348px) and (min-width: 992px) {
  .hub_download_grid {
    grid-template-columns: repeat(3, 1fr); /* 4 at 3 col */
  }
}
/* Media query pour passer à 2 colonnes entre 1227px et 991px */
@media (max-width: 1227px) and (min-width: 991px) {
  .blog_all-articles_collection-list {
    grid-template-columns: repeat(2, 1fr); /* 2 colonnes */
  }
}

.button.is-text.is-icon {
  display: flex; /* Définit le conteneur comme un flexbox */
  flex-wrap: nowrap; /* Empêche les éléments enfants de passer à la ligne */
}
